import { GET_VIDEOGAMES } from "./actionTypes";

const inicialState = {
    videogames: [],
    detail: {},
    genres: [],
    loading: false
}

function reducer (state=inicialState, action) {
    switch(action.type){
        case GET_VIDEOGAMES:
            return {
                ...state,
                videogames: action.payload
            }
        default: 
            return state;
    }
}

export default reducer;